.detail-swiper {
  position: relative;
  width: 100%;
  background: #FFF;
  overflow: hidden;
  border-radius: 0;
  z-index: 1;
  min-height: 25rem;
  /* 轮播基础样式 - 类名改为carousels */
  /* 标题样式 */
