}
.detail-swiper .swiper-container {
  width: 100%;
  min-height: 25rem;
}
.detail-swiper .swiper-container .swiper-slide {
  margin-top: 0;
}
.detail-swiper .swiper-container .adm-swiper-indicator .adm-page-indicator-dot {
  width: 1.5625rem;
}
.detail-swiper .swiper-container .carousel-item {
  min-height: 25rem;
}
.detail-swiper .arrow-left {
  width: 3.125rem;
  height: 3.125rem;
  position: absolute;
  z-index: 15;
  top: 0;
  left: 0;
  background-image: url('/limeet_logo_200_24_02.png');
  background-position: center;
  background-size: 70%;
  background-repeat: no-repeat;
  cursor: pointer;
  opacity: 0.7;
  z-index: 9999;
  display: block;
  display: inline-block;
  text-indent: -3.125rem;
}
.detail-swiper .arrow-left:hover {
  background-image: url('/limeet_logo_200_24_01.png');
}
.detail-swiper .carousels {
  position: relative;
  width: 100%;
  overflow: hidden;
  border: none;
}
.detail-swiper .carousels-inner {
  display: flex;
  height: 100%;
  transition: transform 0.3s ease;
}
.detail-swiper .carousels-item {
  flex-shrink: 0;
  width: 100%;
  position: relative;
}
.detail-swiper .carousels-item img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.detail-swiper .carousels-title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.46875rem;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.7));
  color: white;
  font-size: 0.5625rem;
  font-weight: 500;
}
.detail-swiper .thumbnails-title {
  padding: 0.25rem;
  font-size: 1rem;
  color: #333;
  background-color: white;
}
.detail-swiper .text-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.detail-swiper .text-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.detail-swiper .carousels-indicators {
  position: absolute;
  bottom: 0.46875rem;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 0.1875rem;
  z-index: 10;
}
.detail-swiper .carousels.has-title .carousels-indicators {
  bottom: 1.875rem;
}
.detail-swiper .indicator {
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background-color: rgba(200, 200, 200, 0.5);
  cursor: pointer;
  transition: all 0.3s ease;
}
.detail-swiper .indicator.active {
  background-color: rgba(144, 144, 144, 0.5);
  width: 0.75rem;
  border-radius: 0.125rem;
}
.detail-swiper .carousels-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease, background-color 0.3s ease;
  z-index: 10;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.detail-swiper .carousels:hover .carousels-control {
  opacity: 1;
}
.detail-swiper .carousels-control:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.detail-swiper .control-prev {
  left: 0.46875rem;
}
.detail-swiper .control-next {
  right: 0.46875rem;
}
.detail-swiper .thumbnails-container {
  display: flex;
  gap: 0.3125rem;
  margin-top: 0.46875rem;
  padding: 0.3125rem 0;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #ccc transparent;
}
.detail-swiper .thumbnails-container::-webkit-scrollbar {
  height: 0.1875rem;
}
.detail-swiper .thumbnails-container::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 0.09375rem;
}
.detail-swiper .thumbnail {
  width: 3.75rem;
  border-radius: 0.125rem;
  overflow: hidden;
  cursor: pointer;
  opacity: 0.7;
  transition: all 0.3s ease;
  flex-shrink: 0;
  background-color: #f5f5f5;
}
.detail-swiper .thumbnail.active {
  opacity: 1;
  transform: scale(1.03);
  box-shadow: 0 0.0625rem 0.25rem rgba(0, 0, 0, 0.2);
}
.detail-swiper .thumbnail-image {
  width: 100%;
  height: 2.5rem;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
}
.page-detail-sale-sku {
  background-color: #fff;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
}
.page-detail-sale-sku .price-wrap {
  background-color: #FFF5E2;
  padding: 0.3125rem 0.9375rem;
}
.page-detail-sale-sku .price-wrap .price {
  padding-top: 0;
  padding-bottom: 0;
  line-height: 1.875rem;
  vertical-align: bottom;
  height: 1.9375rem;
  overflow: hidden;
}
.page-detail-sale-sku .price-wrap .price i.unit {
  color: #EE4D2D;
  vertical-align: bottom;
  padding-right: 0.3125rem;
  display: inline-block;
  overflow: hidden;
  height: 1.5625rem;
}
.page-detail-sale-sku .price-wrap .price span.value {
  color: #EE4D2D;
  font-size: 1.5rem;
  vertical-align: bottom;
}
.page-detail-sale-sku .price-wrap .price span.original-value {
  color: #EE4D2D;
  font-size: 1rem;
  vertical-align: bottom;
  background-color: #FFF5E2;
  padding: 0.0625rem 0.15625rem;
  margin-left: 0.625rem;
}
.page-detail-sale-sku .price-wrap .price span.del-value {
  text-decoration: line-through;
  color: #999;
  vertical-align: bottom;
  display: inline-block;
  overflow: hidden;
  height: 0.75rem;
  line-height: 0.75rem;
  vertical-align: sub;
  margin-left: 0.625rem;
}
.page-detail-sale-sku .price-wrap .title {
  line-height: 1.5rem;
  color: #000;
  font-weight: 400;
  width: 100%;
  height: 100%;
  padding-bottom: 0.3125rem;
}
.page-detail-sale-sku .sale-sku {
  width: 100%;
  height: auto;
  margin: 0.3125rem auto;
  position: relative;
  z-index: 9;
}
.page-detail-sale-sku .sale-sku ul {
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.page-detail-sale-sku .sale-sku ul li.item {
  min-width: 19%;
  max-width: 28%;
  border: 0.0625rem solid #FFF5E2;
  margin-right: 0.3125rem;
  margin-bottom: 0.3125rem;
  font-size: 1rem;
  cursor: pointer;
}
.page-detail-sale-sku .sale-sku ul li.item .box {
  background-color: #fff;
  position: relative;
  clear: both;
}
.page-detail-sale-sku .sale-sku ul li.item .box .show-img {
  width: 15rem;
  height: 15rem;
  padding: 0.625rem;
  position: absolute;
  top: -15.625rem;
  left: 0.3125rem;
  border: 0.0625rem solid #FFF5E2;
  background: #fff;
  display: none;
  z-index: 99;
}
.page-detail-sale-sku .sale-sku ul li.item .box .show-img img {
  width: 13.4375rem;
  height: 13.4375rem;
}
.page-detail-sale-sku .sale-sku ul li.item .pattern {
  display: block;
  float: left;
  width: 3.125rem;
  height: 3.125rem;
}
.page-detail-sale-sku .sale-sku ul li.item .pattern-name {
  display: block;
  padding-left: 0.3125rem;
  float: left;
  width: 3.5rem;
  height: 3.125rem;
  line-height: 3.125rem;
}
.page-detail-sale-sku .sale-sku ul li.item .sale-value {
  display: block;
  padding-left: 0.3125rem;
  float: left;
  width: 4.53125rem;
  height: 3.125rem;
  line-height: 3.125rem;
}
.page-detail-sale-sku .sale-sku ul li.item:hover {
  border: 0.0625rem solid #FF8F00;
}
.page-detail-sale-sku .sale-sku ul li.item:hover .show-img {
  display: block;
  border: 0.0625rem solid #FF8F00;
}
.page-detail-sale-sku .sale-sku ul li.item:hover .pattern-name {
  color: #ee4d2d;
}
.page-detail-sale-sku .sale-sku ul li.current {
  border: 0.0625rem solid #FF8F00;
}
.page-detail-sale-sku .sale-sku ul li.current .pattern-name {
  color: #ee4d2d;
}
.page-detail-sale-sku .sale-sku ul img {
  width: 3.125rem;
}
.page-detail-sale-sku .submit-button {
  height: 2.8125rem;
  margin: 0.625rem auto;
  background-color: #FFA41C;
  border: 0.03125rem solid #FF8F00;
  box-shadow: 0 0.0625rem 0.15625rem 0 #000;
  text-align: center;
  line-height: 2.8125rem;
  border-radius: 1.25rem;
  color: #000;
  font-size: 1.1875rem;
  font-weight: 400;
  cursor: pointer;
}
.page-detail-sale-sku .submit-button:hover {
  opacity: 0.5;
}
.page-detail-sale-sku a {
  color: #000;
}
.page-detail-sale-sku .rich-text-editor a {
  color: #06c;
}
